import React, { useEffect, useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";
import { db } from "../firebase";
import { collection, getDocs, addDoc, deleteDoc, getFirestore, doc} from "firebase/firestore"

function App() {

  //Setup "notes" State
  const [notes, setNotes] = useState([]);

  //Load data from database
  const notesCollectionRef = collection(db, "notes")
  useEffect(() => {

    const getData = async () => {
      const data = await getDocs(notesCollectionRef)
      const arrayData = data.docs.map(doc => ({ id: doc.id, ...doc.data() }))
      const notes = data.docs.map(doc => ({ ...doc.data() }))
      console.log(data)
      console.log(arrayData)
      console.log(notes);
      setNotes(arrayData)
    }

    getData()

  }, [])

  //Adding new note
  const addNoteToDatabase = async (newNote) => {
    await addDoc(notesCollectionRef, { title: newNote.title, content: newNote.content });
  }
  function addNote(newNote) {
    addNoteToDatabase(newNote)
    setNotes(prevNotes => {
      return [...prevNotes, newNote];
    });
  }

  // //editing Note
  function editNote(id) {

  }

  //Delete existing note

  const deleteNoteFromDatabase = async (id) => {
    const toDelete = doc(db, "notes", id)
    await deleteDoc(toDelete)
  }
  function deleteNote(deleteId) {
    deleteNoteFromDatabase(deleteId)

    setNotes(prevNotes => {
      return prevNotes.filter((noteItem, index) => {
        return noteItem.id !== deleteId;
      });
    });
  }



  return (
    <div>
      <Header />
      <CreateArea onAdd={addNote} />
      {notes.map((noteItem, index) => {
        return (
          <Note
            key={index}
            id={noteItem.id}
            title={noteItem.title}
            content={noteItem.content}
            onDelete={deleteNote}
            onEdit={editNote}
          />
        );
      })}
      <Footer />
    </div>
  );
}

export default App;

